@import url(https://fonts.googleapis.com/css?family=Montserrat:400,500,600,700&display=swap&subset=cyrillic);
html{font-family:'Montserrat', sans-serif}img{max-width:100%}.movie-list{-webkit-columns:3;-moz-columns:3;columns:3;grid-column-gap:15px;-webkit-column-gap:15px;-moz-column-gap:15px;column-gap:15px}.movie{position:relative;cursor:pointer;display:inline-block;width:100%;margin-bottom:10px}.movie img{display:block;-webkit-transition:all 0.2s;transition:all 0.2s;width:100%}.movie__title{position:absolute;opacity:0;-webkit-transition:all 0.2s;transition:all 0.2s;z-index:1;color:#fff;text-align:center;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);left:25px;right:25px;font-size:40px;margin:0;font-weight:500}.movie__desc{font-size:25px;display:inline-block;margin-top:10px}.movie:hover img{-webkit-filter:brightness(55%);filter:brightness(55%)}.movie:hover .movie__title,.movie:hover .movie__desc{opacity:1}

